import "../Styles/Elementor/elementor.scss";

class WidgetHandlerClass extends elementorModules.frontend.handlers.Base {
    getDefaultSettings() {
        return {
            selectors: {
                firstSelector: "#17",
            },
        };
    }

    getDefaultElements() {
        const selectors = this.getSettings("selectors");
        return {
            $firstSelector: this.$element.find(selectors.firstSelector),
            $secondSelector: this.$element.find(selectors.secondSelector),
        };
    }

    bindEvents() {
        console.log(this.elements.$firstSelector);
        this.elements.$firstSelector.on("load", this.onFirstSelectorClick.bind(this));
    }

    onFirstSelectorClick(event) {
        console.log(event);
        alert("hello");
    }
}

jQuery(window).on("elementor/frontend/init", () => {
    const addHandler = ($element) => {
        elementorFrontend.elementsHandler.addHandler(WidgetHandlerClass, {
            $element,
        });
    };

    elementorFrontend.hooks.addAction(
        "frontend/element_ready/sheets-to-wp-table-sync-live",
        addHandler
    );
});
